import {
	mapState,
	mapMutations
} from 'vuex'
export default {
	computed: {
		...mapState(['userHeadimg','userName','gender'])
	},
	
	data() {
		let offsetTop = 0;



		return {
			offsetTop: offsetTop,
			popupShow: false
		}
	},
	onLoad() {
		// console.log(this.userHeadimg)
	},
	methods: {
		...mapMutations(['changeHeadimg','changeGender','logout']),
		chooseImage() {
			uni.chooseImage({
				sourceType: ['camera','album'],
				sizeType: ['compressed'],
				count: 1,
				success: (res) => {
					this.changeHeadimg(res.tempFilePaths);
				}
			})
		},
		go(url) {
			uni.navigateTo({
				url: url
			})
		},
		show(){
			this.popupShow = true;
		},
		hide() {
			this.popupShow = false;
		},
		chooseGender(g) {
			this.changeGender(g);
			this.popupShow = false;
		},
		logout1() {
			this.logout();
			uni.navigateBack();
		}
	}
}
